.section5 {
  padding: 100px 0px;
}
.section5-header-text {
  margin-bottom: 80px;
  .container {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  h1 {
    margin: auto;
    width: 520px;
    font-size: 48px;
    line-height: 170%;
    color: #ffffff;
    max-width: 100%;
  }
}
.section5-card.active {
  background: linear-gradient(
    144.39deg,
    #ffffff -278.56%,
    #6d6d6d -78.47%,
    #11101d 91.61%
  );
}
.section5-card {
  position: relative;
  width: 100%;
  height: 425px;

  backdrop-filter: blur(5px);
  border-radius: 20px;
  padding: 60px 45px;
  p:nth-child(2) {
    margin-top: 40px;
    font-family: "Poppins";
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 210%;
    width: 320px;
    letter-spacing: 0.02em;
    color: #ffffff;
  }
  .section5-user {
    align-items: center;
    position: absolute;
    display: flex;
    gap: 20px;
    bottom: 40px;
    img {
      width: 48px;
      height: 48px;
    }
    p {
      margin-bottom: 0px;
    }
  }
}

@media screen and (max-width: 1350px) {
  .section5 {
    .section5-header-text .container {
      flex-direction: column;
      text-align: center;
      h1 {
        margin-bottom: 20px;
        width: 100%;
      }
    }
    .section5-card {
      text-align: center;

      p:nth-child(2) {
        width: 100%;
        font-size: 15px;
      }
      .section5-user {
        p,
        .pGrey {
          font-size: 13px;
        }
      }
    }
  }
}

@media screen and (max-width: 991px) {
  .section5 {
    .section5-card {
      padding: 60px 25px;
      p:nth-child(2) {
        width: 100%;
        font-size: 13px;
      }
      .section5-user {
        p,
        .pGrey {
          font-size: 12px;
        }
      }
    }
  }
}

@media screen and (max-width: 770px) {
  .section5 {
    .section5-header-text {
      h1 {
        font-size: 38px;
      }
    }
    padding: 50px 0px;
    .section5-card {
      margin-bottom: 50px;
      box-shadow: 0 5px 5px -3px #333;
      .section5-user {
        left: 50%;
        transform: translateX(-50%);
      }
    }
  }
}
